.imgg {
    filter: invert(1);
}

#up-scroll {
    filter: invert(10%);
}

#service-img {
    filter: invert(30%);
    margin-top: 4px;
    margin-bottom: 0px;
    transition: all .20s ease-in-out;
    /* transition-timing-function: linear; */
}

#service-img:hover {
    filter: invert(0%);
}

#up-scroll:hover {
    filter: invert(30%);
}

#imggg {
    filter: invert(1);
}

.card-btn {
    width: 100px;
    height: 32px;
}

#md-imgg {
    height: 130px;
}

#imgg-span {
    margin-top: 10px;
    background-color: rgb(237, 237, 237);
    font-size: 13px;
    display: flex;
}

#web-foot {
    font-size: 14px;
    text-decoration: none;
}

#crd2 {
    object-fit: cover;
    height: 100%;
    width: 100%;
    transition: all .50s ease-in-out;
    transition-timing-function: linear;
}

#crd2 :hover {
    transform: scale(0.995);
    color: rgba(var(--bs-success-rgb), var(--bs-text-opacity))
}

#crd-foot2 {
    margin-bottom: 14px;
}

#mid-rows {
    overflow-y: hidden;
    overflow-x: auto;
    scroll-behavior: smooth;
}

#crd-title {
    line-height: 1.2;
    vertical-align: baseline;
    transition: all .50s ease-in-out;
    transition-timing-function: linear;
}

#crd-title:hover {
    text-decoration: underline;
    color: rgba(var(--bs-success-rgb), var(--bs-text-opacity))
}

#news-anchor {
    font-family: Shentox, medium;
}

#des {
    vertical-align: baseline;
}

#crd-btn {
    width: 100px;
    text-decoration: none;
    color: rgb(104, 104, 104);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    vertical-align: baseline;
    font-size: 17px;
    transition: all .50s ease-in-out;
    transition-timing-function: linear;
}

#crd-btn:hover {
    text-decoration: underline;
    color: rgb(0, 0, 0);
}

#crd-img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    transition: transform .40s ease-in-out;
    transition-timing-function: linear;
}

#crd-img:hover {
    object-fit: cover;
    height: 100%;
    width: 100%;
    transform: scale(0.98);
}

#crd-bdge {
    color: rgb(104, 104, 104);
}

#crd-bdge:hover {
    text-decoration: underline;
    color: rgb(41, 41, 41);
}

#top-title {
    margin-top: 5px;
}




#nav-cardd {
    border-top: 1px solid rgb(199, 199, 199);
    border-bottom: 1px solid lightgray;
}

#link-items {
    --bs-text-opacity: 1;
    color: rgba(var(--bs-success-rgb), var(--bs-text-opacity)) !important;
    font-weight: 400;
    font-size: 18px;
}

#link-items:hover {
    --bs-text-opacity: 1;
    color: rgba(var(--bs-dark-rgb), var(--bs-text-opacity)) !important;
    font-weight: 500;
    text-decoration: underline;
}

#link-items:active {
    background-color: rgb(21, 148, 63);
    --bs-text-opacity: 1;
    color: rgba(var(--bs-light-rgb), var(--bs-text-opacity)) !important;
    border: 0px;
}

#link-items:focus {
    background-color: rgb(21, 148, 63);
    --bs-text-opacity: 1;
    color: rgba(var(--bs-light-rgb), var(--bs-text-opacity)) !important;
    border: 0px;
}

