@media (max-width:745px) {
    #crd-box {
        background-color: rgb(243, 243, 243);
    }

    #des {
        display: none;
    }

    #crd-title {
        font-size: 15px;
        padding: 0px;
    }

    #crd-btn {
        font-size: 14px;
        width: 79px;
        height: 28px;
        font-weight: normal;
        position: relative;
        top: -10px;
    }

    #crd-cont {
        margin-bottom: 17px;
        display: none;
    }

    #top-title {
        margin-top: 5px;
        font-size: 16px;
    }

    #crd-bdge {
        font-size: 15px;
    }

    #crd-img {
        object-fit: cover;
        height: 100%;
        width: 100%;
    }

    #md-imgg {
        height: 180px;
    }

    #header {
        font-size: 20px;
    }

    #header2 {
        font-size: 20px;
    }

}


#about-head1 {
    width: 100%;
    text-align: start;
    line-height: 0.1em;
    margin: 10px 0 0px;
    font-size: 28px;
    font-weight: 600;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

#about-head2 {
    width: 100%;
    text-align: start;

    line-height: 0.1em;
    margin-top: 130px;
    font-size: 18px;
    font-weight: 500;
    font-family: "Barlow", sans-serif;
}

#about-para {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    opacity: calc(0.9);
}

#about-img {
    width: 100%;
    transform: scale(1);
    transition: all 0.6s ease-out 0s;
}

#about-img:hover {
    object-fit: inherit;
    transform: scale(1.02);
}